import React from 'react'
import PropTypes from 'prop-types'


class EditBurgerForm extends React.Component{
    static propTypes = {
        burger: PropTypes.shape({
            name: PropTypes.string,
            price: PropTypes.number,
            status: PropTypes.string,
            desc: PropTypes.string,
            image: PropTypes.string
        }),
        index: PropTypes.string,
        updateBurger: PropTypes.func,
        deleteBurger: PropTypes.func
    }

    handleChange = (event) => {
        // console.log(event.currentTarget.value)
        const updatedBurger = {...this.props.burger,
                            // name: event.currentTarget.value // single update
                            [event.currentTarget.name]: event.currentTarget.name === 'price' 
                            ? parseFloat(event.currentTarget.value) || 0 // if 'price' than value convert in number
                            : event.currentTarget.value}
        
        this.props.updateBurger(this.props.index, updatedBurger)
    }

    render () {
        return(
            <div className='burger-edit' >
                 <input 
                       name='name' type='text' 
                       value={this.props.burger.name} 
                       onChange={this.handleChange}
                       />

                <input  
                       name='price' 
                       type='text' 
                       value={this.props.burger.price} 
                       onChange={this.handleChange}
                       />

                <select  name='status'
                         className='status' 
                         value={this.props.burger.status}
                         onChange={this.handleChange} >
                    <option value='available' >Available</option>
                    <option value='unavailable'>Delete from Menu</option>
                </select>

                <textarea  name='desc' 
                           value={this.props.burger.desc}
                           onChange={this.handleChange}/>

                <input  
                       name='image' 
                       type='text' 
                       value={this.props.burger.image} 
                       onChange={this.handleChange}
                       />
                       
                <button onClick={
                    ()=> this.props.deleteBurger(this.props.index)
                    }>Delete Burger</button>

            </div>
        )
    }
}

export default EditBurgerForm