import React from 'react'
import PropTypes from 'prop-types'

const TopBar = props => {
    return (
             <div className="top-bar">
                <div className='top-bar-cart' onClick={props.showSidebar}>
                    <img  src="./images/cart.png" style={{zoom:"40%"}}/>
                </div>
                <div className='search-block'>
                    <div className='searchInput'>
                        <input type = "text" placeholder="Search ..." /* onChange={writeSearchWords}*//>
                    </div> 
                    <div className='searchIcon' > 
                        <img src="./images/search.png" style={{zoom:"40%"}} /> 
                    </div>
                </div>
                <div className='top-bar-cart' onClick={props.showAdminMenu}>
                    <img src="./images/settings.png" style={{zoom:"40%"}}/>
                </div>
            </div>
    )
}

TopBar.propTypes = {

}

export default TopBar
