import React from "react";

class Shipment extends React.Component{

    render () {
        const {total} = this.props
        const shipping = total > 0 && total < 500 ? 350 : 99
        const shippingNeon = shipping === 99 ? (
            <span className='font-effect-neon total-wrap-cheap'>{shipping} $</span>
        ) : (
            <span>{shipping} $</span>
        )


        return(

            <div className='total'>
                <div className='total_wrap'>
                    <div>
                        <div>Shipping: {total > 0 ? shippingNeon : null   }</div>
                        <div className='total_wrap-free'>{total < 500 ? `For free shipping need ${500 - total}`  : null}</div>
                    </div>
                    <div className='total_wrap-final'>
                        Summary: {total}                    
                    </div>
                </div>                    
            </div>
        )
    }
}

export default Shipment