import React, {useState} from "react"
import restaurants from '../sample-restaurants'
import PropTypes from 'prop-types'

const Landing = props => {
 //     state = {
//         display: false,
//         title: '',
//         url:''
//     }
    const [display, setDisplay] = useState(false)
    const [title, setTitle] = useState('')
    const [url, setUrl] = useState('')


    const displayList = () => {
        setDisplay(!display)
    }

    const getTitle = restaurant => {
        const { title, url} = restaurant
        setTitle(title)
        setUrl(url)
        setDisplay(false)
    }

    const goToRestaurant = () => {
        props.history.push(`/restaurant/${url}`)
    }

    return (
        <div className='restaurant_select'>
        <div className='restaurant_select_top'>
        <div onClick={displayList} 
             className='restaurant_select_top-header   font-effect-outline'
        >
            {title ? title : 'Select restaurant'}</div>
            
            <div className='arrow_picker'>
                <div className='arrow_picker-up'></div>
                <div className='arrow_picker-down'></div>
            </div>
        </div>

        {display ? <div className='restaurant_select_bottom'>
        <ul>
            {restaurants.map(restaurant => {return <li 
                                                    key={restaurant.id}
                                                    onClick={()=>getTitle(restaurant)}
                                                    >{restaurant.title}</li>} )}
        </ul>
        </div> : null}
        { title && !display ? (
        <button onClick={goToRestaurant}>Enter in Restaurant</button>
        ) : null }
    </div>
    )
}

Landing.propTypes = {
        history: PropTypes.object
    }

export default Landing