import React from "react"
import PropTypes from 'prop-types'
import TopBar from "./TopBar"

const Header = props => (
        <>
           <TopBar  showSidebar={props.showSidebar}
                    showAdminMenu={props.showAdminMenu}/>
            <header className='top'>
                <div className='wrap' >
                    <div className='header-content' >
                        <div className='header-rating'>
                            <div  className='header-rating_tag'>Rating:</div>
                            <div  className='header-rating_icon'>★★★★★</div>
                        </div>
                        <div  className='header-divider'>Rating:</div>
                        <h1 className='font-effect-fire-animation'>{props.title}</h1>
                        <h3>
                            <span>
                                Fast delivery hot Burgers
                                <span className='sub-header'>#burgers</span>
                            </span>
                        </h3>
                    </div>
                </div>
            </header>
        </>
        )

Header.propTypes = {
    title: PropTypes.string.isRequired
}       

export default Header