import React from "react"
import PropTypes from 'prop-types'
import Header from "./Header"
import MenuAdmin from "./MenuAdmin"
import Order from "./Order"
import sampleBurgers from '../sample-burgers'
import Burger from "./Burger"
import base from '../base'
// import SingIn from './Auth/SingIn'
import firebase from "firebase/app"

class App extends React.Component{
    static propTypes = {
        match: PropTypes.object
    }

    state = {
        burgers:{},
        order: {},
        sidebar: false,
        adminMenu: false
    }

    showSidebar = () => {
        const sidebar = this.state.sidebar
        this.setState({sidebar: !sidebar})
    }
    showAdminMenu = () => {
        const adminMenu = this.state.adminMenu
        this.setState({adminMenu: !adminMenu})
    }

    componentDidMount() {
        const {params} = this.props.match

        const localStorageRef = localStorage.getItem(params.restaurantId)
        // console.log('localStorageRef',localStorageRef)
        if (localStorageRef) {
        this.setState({order: JSON.parse(localStorageRef)})
        }

        this.ref = base.syncState(`${params.restaurantId}/burgers`, {
            context: this,
            state: 'burgers'
        })
    }

    componentDidUpdate() {
        const {params} = this.props.match // get restaurant
        localStorage.setItem(params.restaurantId, JSON.stringify(this.state.order))
    }

    componentWillUnmount () {
        base.removeBinding(this.ref)
    }

    addBurger =(burger)=> {
        // console.log(burger)
        const burgers = {...this.state.burgers} //copy obj from state
        burgers[`burger${Date.now()}`] = burger  //add new burger in value 'burger'
        this.setState({burgers}) //push new obj burgers in state
    }
    updateBurger =(key, updatedBurger)=> {
        const burgers = { ...this.state.burgers } //copy obj from state
        burgers[key] = updatedBurger  //update burger
        this.setState({ burgers }) //push updated burger in state
    }
    deleteBurger =(key)=> {
        // console.log(burger)
        const burgers = {...this.state.burgers} //copy obj from state
        //delete burgers[key] // without database
        burgers[key] = null // delete
        this.setState({burgers}) //push new obj burgers in state
    }

    deleteFromOrder =(key)=> {
        // console.log(burger)
        const order = {...this.state.order} //copy obj from state
        delete order[key] // without database
  
        this.setState({ order }) //push new obj order in state
    }


    loadSampleBurgers =() => {
        // console.log('loadSampleBurgers')
        this.setState({burgers: sampleBurgers})
    }

    addToOrder = (key) => {
        const order = {...this.state.order}   // copy obj from state
        order[key] = order[key] + 1 || 1 // add key with value "1" or refresh present value
        this.setState({order})  // push new obj in state
    }

    handleLogout = async () => {
        await firebase.auth().signOut()
        window.location.reload()
    }

    render () {
        return(
            // <SingIn>
            <>
                {/* --------mobile----------- */}
                {this.state.sidebar 
                ? 
                <div className="ordersMobile"><Order burgers={this.state.burgers} 
                       order={this.state.order}
                       deleteFromOrder={this.deleteFromOrder}/></div>
                : null}
                {this.state.adminMenu 
                ? 
                <div className="menuAdminMobile"><MenuAdmin addBurger={this.addBurger}
                           burgers={this.state.burgers}
                           loadSampleBurgers={this.loadSampleBurgers}
                           updateBurger={this.updateBurger}
                           handleLogout={this.handleLogout}
                           deleteBurger={this.deleteBurger}
                           /></div>
                : null}
                  {/* --------mobile end----------- */}
            <div className='burger-paradise'>
                <div className='menu' style={{margin:"0"}} >
                    <Header title="Very Hot Burger" 
                            amount={10}
                            showSidebar={this.showSidebar}
                            showAdminMenu={this.showAdminMenu}
                            hot={true} />
                    
                    <ul className='burgers'>
                    {Object.keys(this.state.burgers).map(key => {
                        return <Burger key={key}
                                       index={key}
                                       addToOrder={this.addToOrder}
                                       details={this.state.burgers[key]}
                                       />
                        })}
                    </ul>

                </div>

                {this.state.sidebar 
                ? 
                <Order burgers={this.state.burgers} 
                       order={this.state.order}
                       className="ordersDesktop"
                       deleteFromOrder={this.deleteFromOrder}/> 
                : null}

                {this.state.adminMenu 
                ? 
                <MenuAdmin addBurger={this.addBurger}
                           burgers={this.state.burgers}
                           className="menuAdminDesktop"
                           loadSampleBurgers={this.loadSampleBurgers}
                           updateBurger={this.updateBurger}
                           handleLogout={this.handleLogout}
                           deleteBurger={this.deleteBurger}
                           />
                : null}
                <div style={{visibility:"hidden"}}></div>
            </div><footer>Course React Petryk Yaroslav</footer>
            </>
            // </SingIn>
        )
    }
}

export default App